  width: 220px;
}

.invest-consalding__title-block {
  margin: 20px;
}

.invest-consalding__title {
  color: white;
  font-weight: 600;
  font-size: 22px;
}

.invest-consalding__content-wrapper {
  display: flex;
  flex-direction: column;
}

.invest-consalding__list {
  list-style: decimal;
}

.invest-consalding__link {
  color: blue;
}

.competiton {
  display: flex;
  width: 100%;
  background-color: rgb(227, 25, 33);
}

.competiton__img {
  height: 130px;
  width: 100px;
}

.competiton__title-block {
  margin: 15px;
}

.competiton__title {
  font-size: 22px;
  font-weight: 600;
  color: white;
}

.competiton__content {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
}

.competiton__info {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
}

.competiton__text-participants {
  margin-left: 15px;
}

.competiton__element-img {
  width: 100px;
  height: 100px;
}

.competiton__text-bold {
  font-weight: 600;
}

.competiton__link {
  margin: 10px;
  color: blue;
  text-decoration: underline;
}

.competiton__list {
  list-style-type: none;
}

.competiton__list-participants {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.competiton__element {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.competiton__element-participants {
  display: flex;
  flex-direction: row;
  width: 200px;
  height: 100px;
  margin-bottom: 10px;
}

.competiton__element-img-participants {
  height: 100px;
  width: 100px;
}

.competiton__advertising {
  margin: 20px 0 0 40px;
  display: flex;
  flex-direction: column;
  width: 30%;
}

.locations {
  width: 100%;
  background-color: rgb(227, 25, 33);
  display: flex;
}

.locations__img {
  width: 200px;
  height: 130px;
}

.locations__title-block {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.locations__title {
  font-size: 22px;
  font-weight: 600;
  color: white;
}

.locations__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.locations__text {
  font-size: 15px;
  color: white;
}

.locations__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.locations__map-title {
  margin: 20px;
  font-size: 22px;
  font-weight: 600;
}

.locations__wrapper-map {
  border: 1px solid black;
}